html {
  font-size: 100%;
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  padding: 0;
  font-family: "Barlow", sans-serif;
  background-color: hsl(0, 0%, 100%);
  color: hsl(230, 29%, 20%);
  position: relative;
  overflow-x: hidden;
}

img {
  width: 100%;
}

header {
  min-width: 100vw;
  padding: 30px 5px;
}

.container {
  width: 88%;
  margin: 0 auto;
}

.absolute {
  background: hsl(202, 22%, 93%);
  width: 50%;
  height: 20.625rem;
  border-bottom-left-radius: 3.4375rem;
  position: absolute;
  top: 0;
  right: 0;
  z-index: -100;
}

button {
  border: none;
  cursor: pointer;
  font-family: "Barlow Condensed", sans-serif;
}

h1,
h2,
h3 {
  margin-top: 0;
  line-height: 1.1;
}

h1 {
  font-size: rem(24);
  margin-bottom: rem(3);
}
@media (min-width: 71.875em) {
  h1 {
    font-size: rem(28);
  }
}

h2 {
  font-size: rem(24);
  margin-bottom: rem(24);
}

a,
a:visited,
a:active {
  text-decoration: none;
  cursor: pointer;
}

.flex {
  width: 100%;
}
.flex .hero_img {
  position: relative;
  height: 15.625rem;
  width: 390px;
  z-index: -1;
  padding: 10px 0 50px;
}
.flex .hero_img img {
  right: -40px;
  position: absolute;
}
.flex .flex_content h3 {
  color: hsl(231, 7%, 65%);
  font-size: 0.9375rem;
  padding-bottom: 12px;
}
.flex .flex_content h3 span {
  background-color: hsl(230, 29%, 20%);
  color: hsl(0, 0%, 100%);
  padding: 3px 6px;
  border-radius: 11px;
}
.flex .flex_content h1 {
  max-width: 17.5rem;
  font-family: "Barlow Condensed", sans-serif;
}
.flex .flex_content p {
  color: hsl(231, 7%, 65%);
  width: 15.625rem;
  padding-bottom: 10px;
}
.flex .flex_content .btn {
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 0.625rem;
}
.flex .flex_content .btn button {
  font-size: 12px;
  border-radius: 5px;
  background: hsl(0, 100%, 68%);
  color: hsl(0, 0%, 100%);
  padding: 10px 12px;
  transition: background 0.25s;
}
.flex .flex_content .btn button:hover {
  background: hsl(0, 77%, 70%);
}
.flex .flex_content .btn button:nth-of-type(2) {
  background: inherit;
  color: hsl(231, 7%, 65%);
}

nav {
  max-width: 20rem;
  margin: 0 auto;
  display: flex;
  justify-content: space-between;
  align-items: center;
  position: relative;
}
nav .logo {
  width: 1.4375rem;
  cursor: pointer;
}
nav .nav_menu {
  display: none;
  position: absolute;
  background: hsl(0, 0%, 100%);
  inset: 0;
  margin: 2.8125rem auto;
  border-radius: 3px;
  box-shadow: -5px 8px 15px hsl(231, 7%, 65%);
  padding: 15px 15px;
  width: 16.25rem;
  height: 12.8125rem;
}
nav .nav_menu .dot {
  display: none;
}
nav .nav_menu hr.line {
  display: block;
  border: 1px solid hsl(202, 22%, 93%);
}
nav .nav_menu a {
  display: block;
  padding: 0.625rem;
  color: hsl(230, 29%, 20%);
  font-family: "Barlow", sans-serif;
  font-weight: bold;
  text-align: center;
  transition: text-decoration 3s;
}
nav .nav_menu a:hover {
  text-decoration: underline;
}
nav .nav_menu a:nth-of-type(4) {
  color: hsl(231, 7%, 65%);
  padding-top: 0.9375rem;
}

.hamburger_icon {
  width: rem(25);
  cursor: pointer;
}
.hamburger_icon img:nth-of-type(1) {
  display: block;
}
.hamburger_icon img:nth-of-type(2) {
  display: none;
}